html.light {
  --background: #fafafa;
  --foreground: #0a0a0b;
  --muted: #71717a;
  --muted-foreground: #52525b;
  --card: #ffffff;
  --card-foreground: #0a0a0b;
  --border: #e4e4e7;
}
* {
  border-color: var(--border);
}
html {
  scroll-behavior: smooth;
}
body {
  background-color: var(--background);
  color: var(--foreground);
  font-family: var(--font-sans), system-ui, sans-serif;
}
